<h2 id="disable-tabs">Disable tabs <a class="header-anchor" href="#disable-tabs" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>disabled</code> to <code v-pre>&lt;tab&gt;</code> to disable it.</p>
<tabs-disable ref="tabs-disable"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tabs</span>&gt;</span>
