  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tab</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"@vue"</span> <span class="hljs-attr">group</span>=<span class="hljs-string">"Dropdown"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>@vue tab.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tab</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"@bootstrap"</span> <span class="hljs-attr">group</span>=<span class="hljs-string">"Dropdown"</span> <span class="hljs-attr">disabled</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>@bootstrap tab.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
