</code></pre>
<h2 id="pills-style">Pills style <a class="header-anchor" href="#pills-style" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>pills</code> to <code v-pre>&lt;tabs&gt;</code> to apply pills style.</p>
<p>Pills are also vertically stackable by adding <code v-pre>stacked</code>, but if you want the nav and content parts to have layout such as 2 column, you may need some custom CSS apply to them.</p>
<tabs-pills-style ref="tabs-pills-style"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tabs</span> <span class="hljs-attr">pills</span>&gt;</span>
