    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Home tab.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tab</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Profile"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Profile tab.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tab</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"Others"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Others tab.<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tabs</span>&gt;</span>
<span class="hljs-comment">&lt;!-- tabs-pills-style.vue --&gt;</span>
</code></pre>
<h2 id="with-callbacks">With callbacks <a class="header-anchor" href="#with-callbacks" aria-hidden="true">&#128279;</a></h2>
<tabs-with-callbacks ref="tabs-with-callbacks"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">tabs</span> @<span class="hljs-attr">change</span>=<span class="hljs-string">"onChange"</span>&gt;</span>
