<h2 id="dynamic-tabs">Dynamic tabs <a class="header-anchor" href="#dynamic-tabs" aria-hidden="true">&#128279;</a></h2>
<p>An example that generate closable tabs using <code v-pre>v-for</code>:</p>
<tabs-dynamic-example ref="tabs-dynamic-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">section</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">tabs</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"index"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">tab</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"tab in tabs"</span> <span class="hljs-attr">:title</span>=<span class="hljs-string">"tab"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"tab"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>Dynamic {{tab}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"close"</span>&gt;</span>Close this tab<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">tab</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"nav-right"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"sm"</span> @<span class="hljs-attr">click</span>=<span class="hljs-string">"push"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">i</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"glyphicon glyphicon-plus"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">i</span>&gt;</span> Add
        <span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">tabs</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">section</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">tabs</span>: [<span class="hljs-string">'Tab 1'</span>],
        <span class="hljs-attr">count</span>: <span class="hljs-number">1</span>,
        <span class="hljs-attr">index</span>: <span class="hljs-number">0</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      push () {
        <span class="hljs-keyword">this</span>.tabs.push(<span class="hljs-string">`Tab <span class="hljs-subst">${++<span class="hljs-keyword">this</span>.count}</span>`</span>)
        <span class="hljs-comment">// open the new tab after created</span>
        <span class="hljs-keyword">this</span>.$nextTick(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.index = <span class="hljs-keyword">this</span>.tabs.length - <span class="hljs-number">1</span>
        })
      },
      close () {
        <span class="hljs-keyword">this</span>.tabs.splice(<span class="hljs-keyword">this</span>.index, <span class="hljs-number">1</span>)
        <span class="hljs-comment">// select prev tab if the closed tab is the last one</span>
        <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.index === <span class="hljs-keyword">this</span>.tabs.length &amp;&amp; <span class="hljs-keyword">this</span>.index &gt; <span class="hljs-number">0</span>) {
          --<span class="hljs-keyword">this</span>.index
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- tabs-dynamic-example.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="tabs-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/tabs/Tabs.vue">Tabs</a> <a class="header-anchor" href="#tabs-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Number</td>
<td></td>
<td></td>
<td>The current tab index, use this to manual change tab index.</td>
</tr>
<tr>
<td><code v-pre>justified</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Use justified style.</td>
