export default {components:{'tabs-example':{"template":"<div class=\"markdown-live-example\"><tabs>\n  <tab title=\"Home\">\n    <p>Raw denim you probably haven't heard of them jean shorts Austin. Nesciunt tofu stumptown aliqua, retro synth master cleanse. Mustache cliche tempor, williamsburg carles vegan helvetica. Reprehenderit butcher retro keffiyeh dreamcatcher synth. Cosby sweater eu banh mi, qui irure terry richardson ex squid. Aliquip placeat salvia cillum iphone. Seitan aliquip quis cardigan american apparel, butcher voluptate nisi qui.</p>\n  </tab>\n  <tab title=\"Profile\">\n    <p>Food truck fixie locavore, accusamus mcsweeney's marfa nulla single-origin coffee squid. Exercitation +1 labore velit, blog sartorial PBR leggings next level wes anderson artisan four loko farm-to-table craft beer twee. Qui photo booth letterpress, commodo enim craft beer mlkshk aliquip jean shorts ullamco ad vinyl cillum PBR. Homo nostrud organic, assumenda labore aesthetic magna delectus mollit. Keytar helvetica VHS salvia yr, vero magna velit sapiente labore stumptown. Vegan fanny pack odio cillum wes anderson 8-bit, sustainable jean shorts beard ut DIY ethical culpa terry richardson biodiesel. Art party scenester stumptown, tumblr butcher vero sint qui sapiente accusamus tattooed echo park.</p>\n  </tab>\n  <tab title=\"@vue\" group=\"Dropdown\">\n    <p>Etsy mixtape wayfarers, ethical wes anderson tofu before they sold out mcsweeney's organic lomo retro fanny pack lo-fi farm-to-table readymade. Messenger bag gentrify pitchfork tattooed craft beer, iphone skateboard locavore carles etsy salvia banksy hoodie helvetica. DIY synth PBR banksy irony. Leggings gentrify squid 8-bit cred pitchfork. Williamsburg banh mi whatever gluten-free, carles pitchfork biodiesel fixie etsy retro mlkshk vice blog. Scenester cred you probably haven't heard of them, vinyl craft beer blog stumptown. Pitchfork sustainable tofu synth chambray yr.</p>\n  </tab>\n  <tab title=\"@bootstrap\" group=\"Dropdown\">\n    <p>Trust fund seitan letterpress, keytar raw denim keffiyeh etsy art party before they sold out master cleanse gluten-free squid scenester freegan cosby sweater. Fanny pack portland seitan DIY, art party locavore wolf cliche high life echo park Austin. Cred vinyl keffiyeh DIY salvia PBR, banh mi before they sold out farm-to-table VHS viral locavore cosby sweater. Lomo wolf viral, mustache readymade thundercats keffiyeh craft beer marfa ethical. Wolf salvia freegan, sartorial keffiyeh echo park vegan.</p>\n  </tab>\n</tabs>\n<!-- tabs-example.vue --></div>"},'tabs-disable':{"template":"<div class=\"markdown-live-example\"><tabs>\n  <tab title=\"Home\">\n    <p>Home tab.</p>\n  </tab>\n  <tab title=\"Profile\" disabled>\n    <p>Profile tab.</p>\n  </tab>\n  <tab title=\"@vue\" group=\"Dropdown\">\n    <p>@vue tab.</p>\n  </tab>\n  <tab title=\"@bootstrap\" group=\"Dropdown\" disabled>\n    <p>@bootstrap tab.</p>\n  </tab>\n</tabs>\n<!-- tabs-disable.vue --></div>"},'tabs-pull-right':{"template":"<div class=\"markdown-live-example\"><tabs>\n  <tab title=\"Home\">\n    <p>Home tab.</p>\n  </tab>\n  <tab title=\"Profile\">\n    <p>Profile tab.</p>\n  </tab>\n  <tab title=\"@vue\" group=\"Dropdown\" pull-right>\n    <p>@vue tab.</p>\n  </tab>\n  <tab title=\"@bootstrap\" group=\"Dropdown\">\n    <p>@bootstrap tab.</p>\n  </tab>\n</tabs>\n<!-- tabs-pull-right.vue --></div>"},'tabs-justified-style':{"template":"<div class=\"markdown-live-example\"><tabs justified>\n  <tab title=\"Home\">\n    <p>Home tab.</p>\n  </tab>\n  <tab title=\"Profile\">\n    <p>Profile tab.</p>\n  </tab>\n  <tab title=\"Others\">\n    <p>Others tab.</p>\n  </tab>\n</tabs>\n<!-- tabs-justified-style.vue --></div>"},'tabs-pills-style':{"template":"<div class=\"markdown-live-example\"><tabs pills>\n  <tab title=\"Home\">\n    <p>Home tab.</p>\n  </tab>\n  <tab title=\"Profile\">\n    <p>Profile tab.</p>\n  </tab>\n  <tab title=\"Others\">\n    <p>Others tab.</p>\n  </tab>\n</tabs>\n<!-- tabs-pills-style.vue --></div>"},'tabs-with-callbacks':{"template":"<div class=\"markdown-live-example\">\n  <tabs @change=\"onChange\">\n    <tab title=\"Home\">\n      <p>Home tab.</p>\n    </tab>\n    <tab title=\"Profile\">\n      <p>Profile tab.</p>\n    </tab>\n    <tab title=\"<i class='glyphicon glyphicon-bell'></i> Alert!\" html-title>\n      <p>This tab has HTML title and callback function!</p>\n    </tab>\n  </tabs>\n</div>",
    methods: {
      onChange (index) {
        if (index === 2) {
          window.alert('You clicked on a tab that has callback function!')
        }
      }
    }
  },'tabs-with-nav-right-slot':{"template":"<div class=\"markdown-live-example\"><tabs>\n  <tab title=\"Home\">\n    <p>Home tab.</p>\n  </tab>\n  <tab title=\"Profile\">\n    <p>Profile tab.</p>\n  </tab>\n  <form slot=\"nav-right\">\n    <select class=\"form-control\" style=\"display: inline-block;width: auto\">\n      <option>option1</option>\n      <option>option2</option>\n      <option>option3</option>\n      <option>option4</option>\n    </select>\n    <btn type=\"success\">Button</btn>\n  </form>\n</tabs>\n<!-- tabs-with-nav-right-slot.vue --></div>"},'tabs-dynamic-example':{"template":"<div class=\"markdown-live-example\">\n  <section>\n    <tabs v-model=\"index\">\n      <tab v-for=\"tab in tabs\" :title=\"tab\" :key=\"tab\">\n        <p>Dynamic {{tab}}</p>\n        <btn type=\"danger\" @click=\"close\">Close this tab</btn>\n      </tab>\n      <template slot=\"nav-right\">\n        <btn size=\"sm\" @click=\"push\">\n          <i class=\"glyphicon glyphicon-plus\"></i> Add\n        </btn>\n      </template>\n    </tabs>\n  </section>\n</div>",
    data () {
      return {
        tabs: ['Tab 1'],
        count: 1,
        index: 0
      }
    },
    methods: {
      push () {
        this.tabs.push(`Tab ${++this.count}`)
        // open the new tab after created
        this.$nextTick(() => {
          this.index = this.tabs.length - 1
        })
      },
      close () {
        this.tabs.splice(this.index, 1)
        // select prev tab if the closed tab is the last one
        if (this.index === this.tabs.length && this.index > 0) {
          --this.index
        }
      }
    }
  }}}</script>


